import { TableChart } from './tableChart.model';

export class DetailsTableChart extends TableChart {
    detailsTable: any;
    showDetails: boolean = false;

    constructor(obj?: any) {
        super(obj);
        if (obj.detailTables) {
            this.detailsTable = new TableChart(obj.detailTables[0]);
        }
    }

    hasDetailsTable() {
        return this.detailsTable ? true : false;
    }
}
